import { Field, Formik, Form } from 'formik';
import { Search } from './SearchForm.styled';
import { useEffect, useRef, useState } from 'react';
import { useDispatch } from 'react-redux';
import { setIsOpen } from 'redux/modal/modalSlice';
import { Arrow, DropArrow } from './SearchFormSVG';
import { useLocation } from 'react-router-dom';

export const SearchForm = ({ initialValues, onSubmit }) => {
  const [selectedBrand, setSelectedBrand] = useState('');
  const [selectedModel, setSelectedModel] = useState('');
  const location = useLocation();
  const carMark = {
    BMW: ['X5', 'M3', 'I3'],
    Toyota: ['Camry', 'Corolla', 'Avalon'],
    Mercedes: ['A-Class', 'B-Class', 'C-Class'],
  };
  const distatch = useDispatch();

  const handleBrandChange = event => {
    const brand = event.target.value;
    setSelectedBrand(brand);
    setSelectedModel('');
  };

  const handleModelChange = event => {
    const model = event.target.value;
    setSelectedModel(model);
  };

  const formikRef = useRef(null);
  useEffect(() => {
    if (formikRef.current) {
      formikRef.current.resetForm({ values: initialValues });
    }
  }, [initialValues]);
  useEffect(() => {
    const searchParams = new URLSearchParams(location.search);
    const favouritesParam = searchParams.get('status');
    console.log(favouritesParam);
    if (favouritesParam) {
      formikRef.current.setFieldValue('status', favouritesParam);
    }
  }, [location.search]);

  const clearForm = () => {
    distatch(setIsOpen(true));
  };
  return (
    <Search>
      <Formik
        initialValues={initialValues}
        onSubmit={values => onSubmit(values)}
        innerRef={formik => (formikRef.current = formik)}
      >
        <Form>
          <div className="topWraper">
            <h3 className="title"> </h3>
            <h5 className="underTitle"> </h5>
            <div className="arrowDiv">
              <label>
                <Field className="fieldLong marg16" as="select" name="status">
                  <option value=""></option>
                  <option value="New"></option>
                  <option value="Used"></option>
                  <option value="Servitude"> </option>
                </Field>
              </label>
              <div className="arrow">
                <DropArrow />
              </div>
            </div>
            <div className="arrowDiv">
              <label>
                <Field
                  className="fieldLong marg16"
                  as="select"
                  name="brand"
                  value={selectedBrand}
                  onChange={handleBrandChange}
                >
                  <option value=""></option>
                  {Object.keys(carMark).map(brand => (
                    <option key={brand} value={brand}>
                      {brand}
                    </option>
                  ))}
                </Field>
              </label>
              <div className="arrow">
                <DropArrow />
              </div>
            </div>

            <div className="arrowDiv">
              <label>
                <Field
                  className="fieldLong marg16"
                  as="select"
                  name="model"
                  value={selectedModel}
                  onChange={handleModelChange}
                >
                  <option value=""> </option>
                  {carMark[selectedBrand]?.map(model => (
                    <option key={model} value={model}>
                      {model}
                    </option>
                  ))}
                </Field>
              </label>
              <div className="arrow">
                <DropArrow />
              </div>
            </div>
            <div className="arrowDiv">
              <label>
                <Field className="fieldLong marg16" as="select" name="region">
                  <option value=""></option>
                  <option value="Kyivska"></option>
                  <option value="Vinnytsia"></option>
                  <option value="Volynsk"></option>
                  <option value="Dniprovska"></option>
                  <option value="Donetsk"></option>
                  <option value="Zhytomyr"></option>
                  <option value="Zakarpattia"></option>
                  <option value="Zaporizhzhia"></option>
                  <option value="Ivano-Frankivsk">-</option>
                  <option value="Kirovohradsk"></option>
                  <option value="Luhansk"></option>
                  <option value="Lviv"></option>
                  <option value="Mykolayivska"></option>
                  <option value="Odesa"></option>
                  <option value="Poltava"></option>
                  <option value="Rivne"></option>
                  <option value="Sumy"></option>
                  <option value="Ternopilsk"></option>
                  <option value="Cherkasy"></option>
                  <option value="Chernivtsi"></option>
                  <option value="Chernihivska"></option>
                  <option value="Kharkivska"></option>
                  <option value="Khersonsk"></option>
                  <option value="Khmelnytska"></option>
                </Field>
              </label>
              <div className="arrow">
                <DropArrow />
              </div>
            </div>
            <div className="shortDiv marg16">
              <label>
                <Field
                  className="fieldTextShort"
                  type="text"
                  name="yearFrom"
                  placeholder="  "
                />
              </label>
              <label>
                <Field
                  className="fieldTextShort"
                  type="text"
                  name="yearTo"
                  placeholder="  "
                />
              </label>
            </div>
            <div className="shortDiv">
              <label>
                <Field
                  className="fieldTextShort"
                  type="text"
                  name="priceFrom"
                  placeholder="   $"
                />
              </label>

              <label>
                <Field
                  className="fieldTextShort"
                  type="text"
                  name="priceTo"
                  placeholder="   $"
                />
              </label>
            </div>
          </div>
          <div className="bottomWraper">
            <h3 className="titleB"> </h3>
            <div className="arrowDiv">
              <label>
                <Field className="fieldLong marg16" as="select" name="bodyType">
                  <option value=""> </option>
                  <option value="Universal"></option>
                  <option value="Sedan"></option>
                  <option value="Cabriolet"></option>
                  <option value="Compartment"></option>
                </Field>
              </label>
              <div className="arrow">
                <DropArrow />
              </div>
            </div>
            <div className="arrowDiv">
              <label>
                <Field className="fieldLong marg16" as="select" name="fuelType">
                  <option value=""></option>
                  <option value="Electro">  </option>
                  <option value="Hibrid"></option>
                  <option value="Gasoline"></option>
                  <option value="Gaseous"></option>
                  <option value="Diesel"></option>
                </Field>
              </label>
              <div className="arrow">
                <DropArrow />
              </div>
            </div>
            <div className="arrowDiv">
              <label>
                <Field
                  className="fieldLong marg16"
                  as="select"
                  name="engineCapacity"
                >
                  <option value="">' </option>
                  <option value="Microliter"> 1,1 </option>
                  <option value="Low-volume"> 1,2  1,7 </option>
                  <option value="Medium-sized"> 1,8  3,3 </option>
                  <option value="Large-capacity"> 3,5 </option>
                </Field>
              </label>
              <div className="arrow">
                <DropArrow />
              </div>
            </div>

            <div>
              <label>
                <Field
                  className="fieldTextLong marg16"
                  type="text"
                  name="mileageTo"
                  placeholder="  "
                />
              </label>
            </div>
            <div className="arrowDiv">
              <label>
                <Field
                  className="fieldLong marg16"
                  as="select"
                  name="driveType"
                >
                  <option value=""></option>
                  <option value="Front"></option>
                  <option value="Posterior"></option>
                  <option value="Full"></option>
                </Field>
              </label>
              <div className="arrow">
                <DropArrow />
              </div>
            </div>

            <div className="arrowDiv">
              <label>
                <Field
                  className="fieldLong marg16"
                  as="select"
                  name="transmission"
                >
                  <option value=""> </option>
                  <option value="Mechanical"></option>
                  <option value="Automatic"></option>
                  <option value="Robotic"></option>
                  <option value="Variable"></option>
                </Field>
              </label>
              <div className="arrow">
                <DropArrow />
              </div>
            </div>
            <div className="arrowDiv">
              <label>
                <Field
                  className="fieldLong marg16"
                  as="select"
                  name="condition"
                >
                  <option value=""> </option>
                  <option value="Completely"> </option>
                  <option value="Repaired">
                      
                  </option>
                  <option value="Unrepaired">
                      
                  </option>
                  <option value="Parts">  / </option>
                </Field>
              </label>
              <div className="arrow">
                <DropArrow />
              </div>
            </div>
            <div className="arrowDiv">
              <label>
                <Field className="fieldLong" as="select" name="color">
                  <option value=""></option>
                  <option value="White"></option>
                  <option value="Black"></option>
                  <option value="Gray"></option>
                  <option value="Red"></option>
                  <option value="Blue"></option>
                  <option value="Pink"></option>
                  <option value="Green"></option>
                  <option value="Orange"></option>
                  <option value="Burgundy"></option>
                  <option value="Brown"></option>
                  <option value="Yellow"></option>
                  <option value="Violet"></option>
                  <option value="Gold"></option>
                  <option value="Silver"></option>
                  <option value="beige"></option>
                  <option value="Multicolor"></option>
                </Field>
              </label>
              <div className="arrow">
                <DropArrow />
              </div>
            </div>
          </div>

          <div className="btnWraper">
            <button className="clearButton" onClick={clearForm} type="button">
                <Arrow />
            </button>
            <button className="submitButton" type="submit">
              
            </button>
          </div>
        </Form>
      </Formik>
    </Search>
  );
};
export default SearchForm;

